<template>
	<div class="prologue-container">
		<div class="prologue-header">
			<div class="prologue-zhong">
				<div class="prologue-fanhui">
					<img src="../assets/nav_back.png" @click="back()">
				</div>
				<div class="prologue-ti">{{toptag_name}}</div>
				<div></div>
			</div>
		</div>
		<div class="prologue-content">
			<mu-paper :z-depth="1" class="demo-loadmore-wrap">
				<mu-container ref="container" class="demo-loadmore-content">
					<mu-load-more @refresh="refresh" @load="load" :loading="loading">
						<mu-list>
							<div class="prologue-bj" v-for="item in page_list">
								<div v-if="data.user_vip == '3' || data.user_vip == '2' || data.user_vip == '1'">
									<template>
										<mu-list-item>
											<mu-list-item-title>
												<div class="prologue-between">
													<div class="prologue-flex">
														<div class="prologue-diyi">{{item.highlight_content}}</div>
													</div>
												</div>
											</mu-list-item-title>
										</mu-list-item>
										<mu-list-item  v-for="(one,index) in item.list" :key="index">
											<mu-list-item-title>
												<div class="prologue-between">
													<div class="prologue-flex">
														<div class="prologue-diyi">{{one.highcontent_content}}</div>
													</div>
													<div class="prologue-button" v-show="!!one.highcontent_highlight_id">
														<div class="prologue-fuzhi" v-clipboard:copy="one.highcontent_content" v-clipboard:success="onCopy" v-clipboard:error="onError">复制</div>
													</div>
												</div>
											</mu-list-item-title>
										</mu-list-item>
									</template>
								</div>
								<div v-if="data.user_vip == '0'">
									<template>
										<mu-list-item>
											<mu-list-item-title>
												<div class="prologue-between">
													<div class="prologue-flex">
														<div class="prologue-diyi">{{item.highlight_content}}</div>
													</div>
												</div>
											</mu-list-item-title>
										</mu-list-item>
										<mu-list-item  v-for="(one,index) in item.list" :key="index">
											<mu-list-item-title>
												<div class="prologue-between">
													<div class="prologue-flex">
														<div class="prologue-diyi">{{one.highcontent_content}}</div>
													</div>
													<div class="prologue-button">
														<div class="prologue-fuzhi" v-clipboard:copy="one.highcontent_content" v-clipboard:success="onCopy" v-clipboard:error="onError">复制</div>
													</div>
												</div>
											</mu-list-item-title>
										</mu-list-item>
									</template>
								</div>
							</div>
						</mu-list>
					</mu-load-more>
				</mu-container>
			</mu-paper>
			<!--<div class="prologue-bj" v-for="item in highlights_list">
				<div class="prologue-between">
					<div class="prologue-flex">
						<div class="prologue-nv">
							<img src="../assets/icon_nv.png">
						</div>
						<div class="prologue-diyi">{{item.highlight_content}}</div>
					</div>
				</div>
				<div class="prologue-between" v-for="one in item.list">
					<div class="prologue-flex">
						<div class="prologue-nv">
							<img src="../assets/icon_nan.png">
						</div>
						<div class="prologue-diyi">{{one.highcontent_content}}</div>
					</div>
					<div class="prologue-button">
						<div class="prologue-fuzhi">复制</div>
					</div>
				</div>
			</div>-->
		</div>
		<div class="prologue-footer">

		</div>
	</div>
</template>

<script>
	import Qs from "qs"
	export default {
		name: 'home',
		data() {
			return {
				msg: '',
				toptagid: '',
				data: [],
				highlights_list: '',
				content: '',
				highlights_list: '10',
//				refreshing: false,
				loading: false,
				text: 'List',
				page_list: [],
				page_num: 1,
				page_total: 0
			}
		},
		methods: {
			back() {
				window.history.back();
			},
			highlights() {
				var that = this;
				var params = {
					toptagid: that.toptag_id,
					content: that.content
				};
				that.$http.post('/special/highlights', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.highlights_list = res.data;
						that.page_list = [];
						that.highlights_list.forEach(function(item,index){

							if(that.data.user_vip == '0'){
								if(index <10){
									that.page_list.push(item);
								}else{
									that.page_list.push({
										highlight_content: '********',
										highlight_id: null,
										list:[
											{
												highcontent_content: '****请充值会员查看更多内容****',
												highcontent_highlight_id: null,
											}
										]
									});
								}
							}else{
								that.page_list.push(item);
							}
						})
						that.page_total = res.data.length;
					}
				})
			},
			refresh() {
				// this.refreshing = true;
				this.page_num = 1;
				this.$refs.container.scrollTop = 0;
				setTimeout(() => {

					this.highlights();

					// console.log(11);
					// this.refreshing = false;
					// this.text = this.text === 'List' ? 'Menu' : 'List';
					// this.highlights_list = 10;
				}, 2000)
			},
			load() {
				var that = this;
				this.loading = true;
				setTimeout(() => {
					this.loading = false;
					if(this.page_num*10 >= this.page_total){
						return;
					}
					this.page_num = this.page_num + 1;
					that.page_list = [];
					this.highlights_list.forEach(function(item,index){

						if(that.data.user_vip == '0'){
							if(index < 10){
								that.page_list.push(item);
							}else{
								if(index < that.page_num*10){
									that.page_list.push({
										highlight_content: '********',
										highlight_id: null,
										list:[
											{
												highcontent_content: '****请充值会员查看更多内容****',
												highcontent_highlight_id: null,
											}
										]
									});
								}
							}
						}else{
							if(index < that.page_num*10){
								that.page_list.push(item);
							}
						}

					});
				}, 2000)
			},
			onCopy: function (e) {
				this.$toast.success('已复制');
      			// alert('You just copied: ' + e.text)
    		},
    		onError: function (e) {
      			// alert('Failed to copy texts')
    		},
    					//			个人信息
			userInfo() {
				var that = this;
				var params = {
					userid: that.user_info.user_id,
//					discussionid: that.discussionid
				};
				that.$http.post('/user/userInfo', Qs.stringify(params), {

				}).then((res) => {
					if(res.meta.code == 200) {
						that.data = res.data;
						that.highlights();
					}
				})
			}
			
		},
		created() {
			// 页面刚加载的时候执行
			if(!!localStorage.getItem('user_info')) {
				this.user_info = JSON.parse(localStorage.getItem('user_info'));
			}
			//			this.toptag_id = this.$route.params.id;
			this.toptag_id = this.$route.query.type;
			this.toptag_name = this.$route.query.typeone;
			
			this.userInfo();

		},
		mounted() {
			// 页面刚加载完 dom 也加载完 的时候执行

		}
	}
</script>

<style scoped>
	.prologue-container {
		width: 100%;
		height: 100%;
		/*background: #FFFFFF;*/
	}
	
	.prologue-header {
		height: 50px;
		background: #FFFFFF;
		line-height: 50px;
		/*border-bottom: 1px solid #eaeaea;*/
	}
	
	.prologue-zhong {
		display: flex;
		justify-content: space-between;
	}
	
	.prologue-fanhui img {
		width: auto;
		height: 1.3rem;
		margin-left: 1.5rem;
		margin-top: 1rem;
	}
	
	.prologue-ti {
		font-size: 1.8rem;
		color: #000000;
		margin-left: -1.5rem;
		font-weight: 550;
	}
	
	.prologue-content {
		width: 100%;
		height: calc(100% - 50px);
		overflow-x: hidden;
		overflow-y: auto;
	}
	
	.prologue-bj {
		background: #FFFFFF;
		margin-bottom: 1.5rem;
	}
	
	.prologue-between {
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-top: 1px solid #EAEAEA;
	}
	
	.prologue-between1 {
		display: flex;
		justify-content: space-between;
		align-items: center;
		/*border-top: 1px solid #EAEAEA;*/
		width: 100%;
	}
	
	.prologue-flex {
		width: 0px;
		flex-grow: 1;
		display: flex;
		align-items: center;
	}
	
	.prologue-button {
		width: 80px;
		text-align: center;
	}
	
	.prologue-nv img {
		width: auto;
		height: 1.7rem;
		margin: 1.5rem 0.8rem 1.1rem 1.5rem;
	}
	
	.prologue-diyi {
		white-space: pre-wrap !important;
		font-size: 1.4rem;
		color: #000000;
		/*padding-left: 1.5rem;
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;
		padding-left: 1rem;*/
		padding: 0.5rem 1.5rem;
	}
	
	.prologue-fuzhi {
		font-size: 1.2rem;
		color: #FEFEFE;
		border: 1px solid #fd5d99;
		border-radius: 15px;
		background: #fd5d99;
		/*padding: 0.3rem 1rem;*/
		/*margin-right: 1rem;*/
		width: 66%;
		margin: 0 auto;
	}
	
	.prologue-dier {
		width: 0px;
		flex-grow: 1;
		box-sizing: border-box;
		font-size: 1.4rem;
		color: #000000;
		padding: 1.5rem 0 1.5rem 4rem;
	}
	
	.demo-loadmore-wrap {
		width: 100%;
		max-width: 360px;
		height: 420px;
		display: flex;
		flex-direction: column;
		.mu-appbar {
			width: 100%;
		}
	}
	
	.demo-loadmore-content {
		flex: 1;
		overflow: auto;
		-webkit-overflow-scrolling: touch;
	}
	/deep/ .demo-loadmore-wrap{
		max-width: none;
		height: 100%;
	}
	/deep/ .mu-paper{
		background: none;
	}
	/deep/ .mu-list{
		padding-top: 0rem;
	}
	/deep/ .container{
		padding: 0;
	}
	/deep/ .mu-item-title{
		min-height: 3rem;
		height: auto !important;
		line-height: none;
		
	}
	/deep/ .mu-item{
		min-height: 3rem;
		height: auto !important;
		padding: 0;
	}
</style>


